import { BlogPost } from '@/type';
import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';

export const blogApi = createApi({
  reducerPath: 'blogApi',
  baseQuery: fetchBaseQuery({
    baseUrl: 'https://67f8fa0b094de2fe6e9ffb9b.mockapi.io/api/blog/',
  }),
  endpoints: (builder) => ({
    getBlogCount : builder.query<BlogPost[] , void>({
      query: ()=> `list`
    }),
    getBlogList: builder.query<BlogPost[], {page:number,limit:number}>({
      query: ({page,limit}) => `list?page=${page}&limit=${limit}`
    }),
    getBlogById : builder.query<BlogPost,number>({
        query:(id)=>`/list/${id}`
    }),
    createBlogPost: builder.mutation<BlogPost, Partial<BlogPost>>({
      query: (newPost) => ({
        url: 'list',
        method: 'POST',
        body: newPost,
      }),
    }),
  }),
});

export const { useGetBlogCountQuery, useGetBlogListQuery , useGetBlogByIdQuery , useCreateBlogPostMutation } = blogApi;
